

.search {
    display: flex;
    justify-content: center;
    /* Додаємо вирівнювання по горизонталі в центр */
    align-items: center;
    /* Додаємо вирівнювання по вертикалі в центр */
    font-size: 20px;
    border: solid 0.2em #ec6d6d;
    display: inline-block;
    position: relative;
    border-radius: 2.5em;
    margin-top: 10px;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%)
}

.searchContainer {
    display: flex !important;
}

.App_contact_list_container__EVyvM {
    display: flex !important;
}

.search input[type=text] {
   
   
    width: 2.5em;
    height: 2.5em;
    
    border: none;
    box-sizing: border-box;
    border-radius: 2.5em;
   
}

.search input[type=text]:focus {
    outline: none;
}

.search input[type=text]:focus,
.search input[type=text]:not(:placeholder-shown) {
    width: 18em;
    transition: width 800ms cubic-bezier(0.68, -0.55, 0.27, 1.55);
}

.search input[type=text]:focus+button[type=reset],
.search input[type=text]:not(:placeholder-shown)+button[type=reset] {
    transform: rotate(-45deg) translateY(0);
    transition: transform 150ms ease-out 800ms;
}

.search input[type=text]:focus+button[type=reset]:after,
.search input[type=text]:not(:placeholder-shown)+button[type=reset]:after {
    opacity: 1;
    transition: top 150ms ease-out 950ms, right 150ms ease-out 950ms, opacity 150ms ease 950ms;
}

.search button[type=reset] {
    background-color: transparent;
    width: 1.4em;
    height: 1.4em;
    border: 0;
    padding: 0;
    outline: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0.55em;
    right: 0.55em;
    transform: rotate(-45deg) translateY(2.2em);
    transition: transform 150ms ease-out 150ms;
}

.search button[type=reset]:before,
.search button[type=reset]:after {
    content: "";
    background-color: #000000;
    width: 0.3em;
    height: 1.4em;
    position: absolute;
}

.search button[type=reset]:after {
    transform: rotate(90deg);
    opacity: 0;
    transition: transform 150ms ease-out, opacity 150ms ease-out;
}